import { Instruccion } from "./Instruccion";

export class Despacho {
  clon(): Despacho {
    var instruccionesClon = new Array<Instruccion>();
    for (let i = 0; i < this.instrucciones.length; i++) {
      instruccionesClon.push(this.instrucciones[i].clon());
    }
    if (this.tamano == undefined)
      this.tamano = this.instrucciones.length;
    return new Despacho(instruccionesClon, this.tamano);
    }
    
  constructor(public instrucciones: Array<Instruccion>, public tamano: number) {

  }
  get() {
    return { instrucciones: this.instrucciones, tamano: this.tamano } as Despacho
  }
  ordenarInstrucciones(): any {
    var a = 5;
  }
}
