import { Instruccion } from "./Instruccion";

export class Estacion {
  ordenar(i: number) {
    if (this.instrucciones[i].name == '') {
      let j = i - 1;
      while ((j >= 0) && (this.instrucciones[j].name != '')) {
        if (this.instrucciones[i].name == '') {
          this.instrucciones[i] = this.instrucciones[j].clon();
          this.instrucciones[j].name = '';
        }
        i--;
        j--;
      }
    }
    }
    clon(): Estacion {
      var instruccionesClon = new Array<Instruccion>();
      for (let i = 0; i < this.instrucciones.length; i++) {
        instruccionesClon.push(this.instrucciones[i].clon());
      }
      if (this.tamano == undefined)
        this.tamano = this.instrucciones.length;
      return new Estacion(instruccionesClon, this.tamano);
    }
  constructor(public instrucciones: Array<Instruccion>,public tamano?:number) {
      
  }
  tengoLugar(): number {

    for (let j = this.instrucciones.length-1; j >= 0; j--) {
      if (this.instrucciones[j].name == '')
        return j;
    }
    return -1;
  }
  nextEstacion(arr: string[]): number {
    for (let j = this.instrucciones.length - 1; j >= 0; j--) {
      if (this.instrucciones[j].name != '') {
        if ((this.instrucciones[j].tipo.name == 'ST') && (this.estaOcupado(arr, this.instrucciones[j].reg1, this.instrucciones[j].regDest) == 'N'))
          return j;
        else if ((this.instrucciones[j].tipo.name == 'LD') && (this.estaOcupado(arr, this.instrucciones[j].reg1) == 'N'))
          return j;
        else if  (this.estaOcupado(arr, this.instrucciones[j].reg1, this.instrucciones[j].reg2) == 'N')
          return j;
      }
    }
    return -1;
  }
  estaOcupado(arr: string[], reg1: string, reg2?: string): string {
    var nombre = reg1.substr(1);
    var numero = +nombre;
    if (reg2 != undefined) {
      var nombre2 = reg2.substr(1);
      var numero2 = +nombre2;
      if (arr[numero2] == 'D' && arr[numero] == 'D')
        return 'N';
      else
        return 'Y';
    }
    if (arr[numero] == 'D')
      return 'N';
    else
      return 'Y';
    }
}
